				<img src="@/assets/image/index-img1.png" width="100%" />
				<p class="fz18">免费试学</p>
				<p class="fz12">先培后付</p>
				<router-link to="/freeStudy" tag="button" class="btn-solid fc-f">立即申请</router-link>
