</template>

<script>
	export default {
		name: 'index',
		data() {
			return {
				
			};
		},
		components : {
			
		},
		mounted(){
			
		},
		created: function(){
			
		},
		methods: {
			//百度地图获取当前的地址信息
			getLocation(){
				baidumap_location.getCurrentPosition(function (result) {
					this.$comm.alertMsg(JSON.stringify(result, null, 4));
				}, function (error) {

				});
			}
		}
	}
