<style scoped="scoped" lang="scss">
	.ico-top-triangle{
		display: inline-block;
		width: 0;
		height: 0;
		border: 5px solid transparent;
		border-top-color: #666;
		margin-left: 4px;
	}
	.state-box{
		padding: 0 15px;
		background: $main-color;
		line-height: 30px;
		position: fixed;
		width: 100%;
		box-sizing: border-box;
		z-index: 1;
	}
	.ico-sweep{
		width: 20px;
		height: 100%;
		background: url(data:image/gif;base64,) no-repeat center;
		background-size: 100% auto;
		margin-right: 15px;
	}
	.place-box{
		position: fixed;
		width: 100%;
		padding: 0 15px 10px;
		box-sizing: border-box;
		z-index: 1;
		
		.placeList{
			li{
				border: 1px solid $gray-c;
				min-width: 100px;
				padding: 0 10px;
				box-sizing: border-box;
				line-height: 30px;
				text-align: center;
				margin: 10px 0 0 10px;
				border-radius: 5px;
			}
		}
	}
	.main-box{
		padding: 15px;
		.box{
			height: 86px;
			box-sizing: border-box;
			padding: 20px 20px 18px 20px;
			img{
				position: absolute;
				z-index: -1;
				width: 100%;
				height: 100%;
				left: 0;
				top: 0;
			}
		}
		
		.btn-solid{
			position: absolute;
			right: 20px;
			bottom: 16px;
			line-height: 24px;
			border-radius: 24px;
			padding: 0 12px;
		}
		
		.notice-box{
			justify-content: space-between;
			.item{
				flex: inherit;
				border: 1px solid $gray-c;
				border-radius: 5px;
				margin-bottom: 10px;
				width: 76px;
