			margin: 20px 0 10px;
		}
		
		.ico-must{
			background: url(data:image/gif;base64,) no-repeat center;
			background-size: cover;
			width: 34px;
			height: 34px;
			margin: 11px auto 2px;
		}
		.ico-mock{
			background: url(data:image/gif;base64,) no-repeat center;
			background-size: cover;
			width: 34px;
			height: 34px;
			margin: 11px auto 2px;
